.logo {
	width: 450px;
	height: 450px;
	animation: rotate 10s infinite linear;
	transition: all 0.5s ease;
	position: relative;
	z-index: 5;
	cursor: pointer;
	& svg {
		width: 450px;
		height: 450px;
		fill: #d5a54e;
	}
}
.gulp {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	transition: all 0.5s ease;
	& img {
		width: 250px;
		height: 150px;
	}
	& svg {
		width: 200px;
		height: 100px;
		fill: #d5a54e;
		& g {
			fill: #d5a54e;
			stroke: #d5a54e;
		}
	}
}
.header {
	&__list {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: flex-end;
		position: absolute;
		left: 20px;
		bottom: 30px;
		&-item {
			align-items: center;
			background-color: transparent;
			border: 1px solid rgba(213, 165, 78, 0.2);
			cursor: pointer;
			display: flex;
			height: 40px;
			justify-content: center;
			transition: 0.4s ease;
			width: 40px;
			color: #d5a54e;
			position: relative;
			margin: 12px 0 0 0;
			&:hover {
				background: #d5a54e;
				transition: 0.4s ease;
				color: #000;
				&.header__list-item p {
					opacity: 1;
					transition: all 0.5 ease;
				}
			}
			& p {
				transition: all 0.5s ease;
				opacity: 0;
				position: absolute;
				top: 55%;
				transform: translate(0%, -55%);
				font-weight: 500;
				min-width: 150px;
				width: 100%;
				right: -160px;
				color: #000;
			}
		}
	}
	&__version {
		position: absolute;
		right: 30px;
		bottom: 30px;
		transition: all 0.5s ease;
		font-weight: 500;
		color: #d5a54e;
		cursor: pointer;
		&:hover {
			transition: all 0.5s ease;
			color: rgb(255, 166, 0);
		}
		& a {
			color: #d5a54e;
		}
	}
}
@keyframes rotate {
	0% {
		transform: rotate(0);
	}
	25% {
		transform: rotate(90deg);
	}
	50% {
		transform: rotate(180deg);
	}
	75% {
		transform: rotate(270deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
